<template>
  <div>
    <van-list v-model="loading" :finished="finished" :immediate-check="false" finished-text="没有更多了" @load="onLoad">
      <ListItem v-for="item in Movielist" :key="item.filmId" :itemData="item" @click.native="goDetail(item.filmId)"> </ListItem>
    </van-list>
  </div>
</template>

<script>
import ListItem from '@/components/ListItem.vue'
export default {
  name: 'NowPlaying',
  components: {
    ListItem,
  },
  data() {
    return {
      loading: false,
      finished: false,
      total: 0,
      Movielist: [],
      pageNum: 1,
    }
  },
  mounted() {
    this.getMoviesList()
  },
  methods: {
    async getMoviesList() {
      const res = await this.$api.GetNowPlayingMovie(this.pageNum)
      this.Movielist = res.data.films
      this.total = res.data.total
    },
    goDetail(id) {
      // console.log(666)
      this.$router.push({ name: 'Detail', params: { id } })
    },
    async onLoad() {
      // 数据全部加载完成
      if (this.Movielist.length === this.total && this.total !== 0) {
        this.finished = true
        return
      }
      // 异步更新数据
      this.pageNum++
      const res = await this.$api.GetNowPlayingMovie(this.pageNum)
      this.Movielist = [...this.Movielist, ...res.data.films]
      // 加载状态结束
      this.loading = false
    },
  },
}
</script>

<style lang="less" scoped></style>
